'use client'

import { Search, Moon, Grid3x3, Bell, Flag } from 'lucide-react'
import { Avatar, AvatarImage, AvatarFallback } from '@/components/ui/avatar'
import {useState, useEffect} from 'react';

export function Header() {
  const [isDarkMode, setIsDarkMode] = useState(false); //false means light mode dark

  useEffect(() =>{
    if(isDarkMode){
      //add a dark class to the html element
      document.documentElement.classList.add('dark')
    }else{
      document.documentElement.classList.remove('dark')
    }
  }, [isDarkMode]); //this runs whenever dark mode changes.

  function toggleDarkMode() {
    setIsDarkMode(prevMode => !prevMode); // this switch the mode. true <-> false
  }
  return (
    <nav className=" bg-white   dark:bg-gray-950 border-b border-slate-700 px-6 py-4 flex items-center justify-between m-2 rounded-lg">
      {/* Left: Search */}
      <div className="flex items-center gap-3 flex-1">
        <Search className="w-5 h-5 text-slate-400" />
        <input
          type="text"
          placeholder="Search (Ctrl+/)"
          className="bg-transparent text-slate-300 placeholder-slate-500 outline-none text-sm w-64"
        />
      </div>

      {/* Right: Icons and Avatar */}
      <div className="flex items-center gap-6">
        {/* Flag Icon */}
        <button className="text-slate-300 hover:text-slate-100 transition">
          <Flag className="w-5 h-5" fill="currentColor" />
        </button>

        {/* Theme Toggle */}
        <button className="text-slate-300 hover:text-slate-100 transition">
          <Moon className="w-5 h-5" onClick={toggleDarkMode} />
        </button>

        {/* Grid Icon */}
        <button className="text-slate-300 hover:text-slate-100 transition">
          <Grid3x3 className="w-5 h-5" />
        </button>

        {/* Notification Bell */}
        <button className="relative text-slate-300 hover:text-slate-100 transition">
          <Bell className="w-5 h-5" />
          <span className="absolute -top-2 -right-2 bg-red-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center font-semibold">
            1
          </span>
        </button>

        {/* User Avatar */}
        <Avatar className="w-8 h-8">
          <AvatarImage src="https://github.com/shadcn.png" alt="User" />
          <AvatarFallback>US</AvatarFallback>
        </Avatar>
      </div>
    </nav>
  )
}
